<template>
    <div class="header-panel df aic">
        <div class="left exit df ffcn aic" @click="$router.back()">
            <span class="iconfont icon-app_icons--"></span> 
            <div>退出</div>
        </div>
        <div class="center df">
            <div slot="reference" class="exit df ffcn aic mr40" @click="$emit('reset')" >  
                <span class="iconfont icon-zhongzhi"></span>
                <div>重置</div>
            </div>
            <div slot="reference" class="exit df ffcn aic mr40" @click="$emit('save')" >
                <span class="iconfont icon-baocun"></span>
                <div>保存</div>
            </div>
            <div slot="reference" class="exit df ffcn aic mr40" @click="$emit('release')" >
                <span class="iconfont icon-fabu"></span>
                <div>立即发布</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "HeaderPanel",
    data() {
        return {};
    },
    methods: {
       
    }
};
</script>
<style scoped>
.header-panel {
    width: 100%;
    height: 55px;
    background-color: #fff;
    position: absolute;
    bottom: 0;
    -moz-box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.1);
    -webkit-box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.1);
    z-index: 11;
    font-size: 14px;
}

.left {
    width: 50px;
}

.exit {
    cursor: pointer;
    transition: all 0.5s;
}

.exit:hover {
    color: #25a589;
    transform: scale(1.05);
}

.center {
    flex: 1;
    display: flex;
    justify-content: center;
}

/* ICONFONT */
.iconfont {
    font-size: 20px;
}
</style>


