<template>
    <div class="element-title ">
        <div class="head">
            <div>
                <span class="iconfont">&#xe7ac;</span>
                <span class="iconfont">&#xe633;</span>
            </div>
            <p>12:00 AM</p>
            <div class="df aic">
                50%
                <span class="iconfont ml5">&#xe695;</span>
            </div>
        </div>
        <div class="w24 df title">
            <div class="title-left">
                <img :src="images.url" alt width="100%" height="100%" />
                {{ images.url ? '' : '请在右侧设置标题栏样式' }}
            </div>
            <div class="title-right">
                <img src="~@/assets/img/微信图片_20125153322.png" width="100%" height="32px" alt="">
            </div>
        </div>

        <img class="bg-img" :src="background_img"  width="100%"  alt="">
    </div>
</template>

<script>
export default {
    name: "ElementTitle",
    props: {
        background_img: String,
        images: Object
    }
};
</script>

<style  scoped>
img {
    vertical-align: middle;
}
.head {
    font-size: 12px;
    display: flex;
    justify-content: space-between;
    padding: 8px;
    align-items: center;
    position: relative;
    z-index: 1;
}

.title {
    height: 45px;
    line-height: 45px;
    position: relative;
    z-index: 1;
}

.title-left {
    width: 278px;
    height: 100%;
    text-align: center;
}

.title-right {
    width: 87px;
    height: 100%;
}

.element-title {
    width: 100%;
    /* height: 80px; */
    position: relative;
    overflow: hidden;
}

.bg-img {
    position: absolute;
    bottom: 0;
    left: 0;
}

/* ICONFONT */
.element-title .iconfont {
    font-size: 16px;
}
</style>



