<template>
    <div class="tooltip-hint">
        <el-popover placement="left" width="150" v-model="visibleVal">
            <div style="text-align: left; margin: 0">
                <div
                    class="insert-item"
                    v-for="item in menuOptions.slice(1,5)"
                    :key="item.value"
                    @click="$emit('elementOperation', 'insert', item.value), visibleVal = false"
                >
                    <span :class="[item.icon, 'plr5']"></span>
                    <span>{{ item.title }}</span>
                </div>
            </div>
            <el-tooltip
                slot="reference"
                class="item"
                effect="dark"
                :content="menuOptions[0].title"
                placement="right"
            >
                <div
                    class="tooltip-hint-item"
                    @click="$emit('elementOperation', menuOptions[0].value)"
                >
                    <span :class=" menuOptions[0].icon "></span>
                </div>
            </el-tooltip>
        </el-popover>

        <el-tooltip
            v-for="item in menuOptions.slice(1)"
            :key="item.value"
            class="item"
            effect="dark"
            :content="item.title"
            placement="right"
        >
            <div class="tooltip-hint-item" @click="$emit('elementOperation', item.value)">
                <span :class=" item.icon "></span>
            </div>
        </el-tooltip>
    </div>
</template>

<script>
export default {
    data() {
        return {
            visibleVal: false,
            menuOptions: [
                {
                    title: "插入",
                    icon: "iconfont icon-charuxing",
                    value: "insert"
                },
                {
                    title: "普通组件",
                    icon: "iconfont icon-tupiancopy",
                    value: "ElementBasis"
                },
                {
                    title: "轮播图样式一",
                    icon: "iconfont icon-lunbo",
                    value: "ElementBanner"
                },
                {
                    title: "轮播图样式二",
                    icon: "iconfont icon-shouyelunbotu",
                    value: "ElementNewBanner"
                },
                {
                    title: "滑动组件",
                    icon: "iconfont icon-zujian-zuoyouhuadongtu",
                    value: "ElementScroll"
                },
                {
                    title: "上移",
                    icon: "iconfont icon-shangyi",
                    value: "move-up"
                },
                {
                    title: "下移",
                    icon: "iconfont icon-xiayi",
                    value: "move-down"
                },
                {
                    title: "复制",
                    icon: "iconfont icon-fuzhi",
                    value: "copy"
                },
                {
                    title: "删除",
                    icon: "iconfont icon-shanchu",
                    value: "remove"
                }
            ]
        };
    }
};
</script>
<style scoped>
.tooltip-hint {
    background-color: #fff;
    border-radius: 4px;
    z-index: 1;
    align-self: center;
    margin-left: 50px;
}

.insert-item {
    transition: all 0.5s;
}

.insert-item:hover {
    cursor: pointer;
    color: #25a589;
    transform: scale(1.05);
}

.tooltip-hint-item:hover {
    background-color: rgba(37, 165, 137, 0.09);
}

.tooltip-hint-item {
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
    text-align: center;
    cursor: pointer;
}

.tooltip-hint-item span {
    font-size: 24px;
}
</style>


