<template>
    <div class="element ">
        <el-row class="center" :style="[style]" v-if="!images.length">请在右侧设置该行的样式 BASIS </el-row>

        <!-- ELEMENT_BASIS  -->
        <el-row class="center df " :style="[style]" v-else>
            <el-col class="heightfull item "  v-for="(item, index) in images" :key="index">
                <el-image :src="item.url">
                    <div slot="error" class="image-slot">
                        <i class="el-icon-picture-outline"></i>
                    </div>
                </el-image>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    props: {
        type: String,
        width: {
            type: Number,
            default: 100
        },
        height: {
            type: Number,
            default: 100
        },
        images: {
            type: Array,
            default() {
                return [];
            }
        }
    },
    computed: {
        style() {
            return {
                height: this.height + "px",
                "line-height": this.height + "px"
            };
        },
    }
};
</script>
<style scoped>
.element {
    width: 100%;
    background-color: #dff9fb;
    overflow: hidden;
}

.center {
    text-align: center;
}

.item {
    flex: 1;
}

/* ELEMENT-UI STYLE */
.element /deep/ .el-image {
    height: 100%;
    width: 100%;
   
}
</style>


