<template>
    <div class="element ">
        <el-row class="center" :style="[style]" v-if="!images.length">请在右侧设置该行的样式 SCROLL</el-row>

        <better-scroll
            class="better-scroll"
            ref="scroll"
            :style="[style]"
            v-else
            v-bind="{ data: images, width: width }"
        >
            <div class="content">
                <div class="item" v-for="(item, index) in images" :key="index">
                    <i v-if="!item.url" class="el-icon-picture-outline" :style="[{ 'width': width + 'px' }]"></i>
                    <img v-else :src="item.url" :width="width+ 'px'" height="100%"/>
                </div>
            </div>
        </better-scroll>
    </div>
</template> 

<script>
import BetterScroll from "@/components/BetterScroll";

export default {
    components: {
        BetterScroll
    },
    props: {
        type: String,
        width: {
            type: Number,
            default: 100
        },
        height: {
            type: Number,
            default: 100
        },
        images: {
            type: Array,
            default() {
                return [];
            }
        }
    },
    computed: {
        style() {
            return {
                height: this.height+ "px",
                "line-height": this.height + "px"
            };
        }
    }
};
</script>
<style scoped>
.better-scroll {
    overflow: hidden;
}

.better-scroll > .content {
    white-space: nowrap;
    display: inline-block;
    text-align: center;
    height: 100%;
}

.item {
    display: inline-block;
    height: 100%;
    text-align: center
}
.center {
    text-align: center;
}

.element {
    width: 100%;
    background-color: #dff9fb;
    overflow: hidden;
}

/* ELEMENT-UI STYLE */
.element /deep/ .el-image {
    height: 100%;
    width: 100%;
}
</style>


