<template>
    <div class="element-banner ">
        <div :style="[ newStyle ]">
            <el-carousel :height="height + 'px'">
                <el-carousel-item
                    v-for="(item, index) in (!images.length ? 3 : images)"
                    :key="index"
                >
                    <h3
                        class="small"
                        :style="[style]"
                        v-if="!item.url"
                    >{{ '请在右侧编辑该行样式'}} {{ index }}</h3>
                    <img :src="item.url"  width="100%" :style="[style]" v-else alt />
                </el-carousel-item>
            </el-carousel>
        </div>
        <img  class="bg-img" :src="background_img" width="100%" height="100%" alt="">
    </div>
</template>

<script>
export default {
    name: "ElementBanner",
    props: {
        type: String,
        images: {
            type: Array,
            default() {
                return [];
            }
        },
        width: {
            type: Number,
            default: 200
        },
        height: {
            type: Number,
            default: 150
        },
        background_img: String
    },
    computed: {
        style() {
            return {
                height: this.height + "px",
                "line-height": this.height + "px"
            };
        },
        newStyle() {
            if (this.type === "ElementNewBanner") {
                return {
                    margin: "0 auto",
                    overflow: "hidden",
                    "border-radius": "8px",
                    position: 'relative',
                    'z-index': 1,
                    width: this.width + "px",
                };
            } else {
                return "";
            }
        },
    }
};
</script>
<style scoped>
.element-banner {
    position: relative;
    overflow: hidden;
}

.bg-img {
    position: absolute;
    top: 0;
    left: 0;
}

/* ELEMENT_UI STYLE */
.element-banner >>> .el-carousel__container > button {
    display: none;
}

.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
}
.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n + 1) {
    background-color: #d3dce6;
}
.element-banner h3 {
    text-align: center;
}
.element-banner /deep/ .el-carousel__button {
    width: 6px;
    height: 6px;
    border-radius: 50%;
}
</style>


