<template>
    <div class="mini-program df jcsb">
        <div class="w24 df jcc container">
            <div class="auto-scroll">
                <!-- 左侧 -->
                <phone-panel>
                    <draggable
                        v-model="elementComponents"
                        @end="end"
                        class="draggable"
                        draggable=".draggable-item"
                        :animation="500"
                    >
                        <transition-group>
                            <component
                                :class="['border-dashed', { 'border-active': currentIndex === index }, { 'draggable-item': index != 0 } ]"
                                :is="item.type === 'ElementNewBanner' ? 'ElementBanner' : item.type "
                                v-for="(item, index) in elementComponents"
                                :key="item.id"
                                @click.native="currentIndex = index"
                                v-bind=" { type: item.type, height: item.height, images: item.images , background_img: item.background_img, width: item.width } "
                            ></component>
                        </transition-group>
                    </draggable>
                </phone-panel>
            </div>

            <!-- 中间 -->
            <setting-panel @elementOperation="add" />
        </div>

        <!-- 右侧 -->
        <div class="right-panel heightfull">
            <component
                :is="settingType"
                :type="elementComponents[currentIndex].type"
                :images="elementComponents[currentIndex].images"
                :width.sync="elementComponents[currentIndex].width"
                :height.sync=" elementComponents[currentIndex].height"
                :background_img.sync="elementComponents[currentIndex].background_img"
                :currentIndex="currentIndex"
            />
        </div>

        <!-- 顶部 -->
        <header-panel @reset="reset" @release="release" @save="save" />

        <!-- 保存 -->
        <el-dialog title="保存信息" :visible.sync="dialogFormVisible" width="500px" class="dialog">
            <el-form :model="form" label-position="right" label-width="55px">
                <el-form-item label="发布到">
                    <el-select v-model="form.value" placeholder="请选择">
                        <el-option v-for="item in saveType" :key="item" :label="item" :value="item"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="描述">
                    <el-input type="textarea" :rows="1" placeholder="请输入内容" v-model="form.describe"></el-input>
                </el-form-item>
                <el-form-item label="封面">
                    <!-- TODO: 保存dialog -->
                    <el-upload class="avatar-uploader" action="#" :show-file-list="false">
                        <!-- :on-change="setBackgroundImg " -->
                        <!-- :before-upload="beforeAvatarUpload" -->
                        <!-- :on-success="handleAvatarSuccess" -->
                        <img v-if=" form.cover" :src="form.cover" class="avatar" />
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import PhonePanel from "./PhonePanel";
import SettingPanel from "./SettingPanel";
import HeaderPanel from "./HeaderPanel";
/* 拖拽组件 */
import Draggable from "vuedraggable";
/* 组件 */
const path = require("path");
const files = require.context("../components", false, /\.vue$/);
const modules = {};
files.keys().forEach(key => {
    const name = path.basename(key, ".vue");
    modules[name] = files(key).default || files(key);
});

export default {
    components: {
        PhonePanel,
        SettingPanel,
        HeaderPanel,
        Draggable,
        ...modules
    },
    data() {
        return {
            /* 保存 */
            dialogFormVisible: false,
            saveType: ["首页", "活动页"],
            form: {
                describe: "",
                value: "",
                cover: ""
            },
            currentIndex: 0,
            elementComponents: [
                {
                    id: 123456,
                    type: "ElementTitle",
                    background_img: "",
                    images: {
                        path: "",
                        url: ""
                    }
                }
            ]
        };
    },
    /* TAG: 配置面板 */
    computed: {
        /**
         *  当前选中的组件对应的配置面板
         */
        settingType() {
            let { elementComponents: el, currentIndex: index } = this;
            let pathName =
                el[index].type === "ElementTitle"
                    ? "SettingTitle"
                    : "SettingBasis";

            return () => import(`../setting/${pathName}`);
        }
    },
    methods: {
        /**
         *  添加组件
         */
        add(value, subclass) {
            let obj = {
                type: value,
                height: 150,
                images: []
            };

            let { elementComponents: element } = this;
            switch (value) {
                /* 添加基础组件 */
                case "ElementBasis":
                    element.push(Object.assign(obj, { id: Date.now() }));
                    this.currentIndex = element.length - 1;
                    break;

                /* 滑动组件 */
                case "ElementScroll":
                    element.push(
                        Object.assign(obj, { id: Date.now(), width: 200 })
                    );
                    // 同时选中刚创建的组件
                    this.currentIndex = element.length - 1;
                    break;

                /* 轮播图组件1 */
                case "ElementNewBanner":
                    element.push(
                        Object.assign(obj, {
                            id: Date.now(),
                            width: 300,
                            background_img: ""
                        })
                    );
                    this.currentIndex = element.length - 1;
                    break;

                /* 轮播图组件2 */
                case "ElementBanner":
                    element.push(Object.assign(obj, { id: Date.now() }));
                    this.currentIndex = element.length - 1;
                    break;

                /* 插入组件 */
                case "insert":
                    let insertObj = {
                        type: subclass,
                        id: Date.now(),
                        height: 150,
                        images: []
                    };
                    if (subclass == "ElementScroll") {
                        element.splice(
                            this.currentIndex + 1,
                            0,
                            Object.assign(insertObj, { width: 200 })
                        );
                        this.currentIndex++;
                    } else if (subclass == "ElementNewBanner") {
                        element.splice(
                            this.currentIndex + 1,
                            0,
                            Object.assign(insertObj, {
                                width: 300,
                                background_img: ""
                            })
                        );
                        this.currentIndex++;
                    } else if (
                        subclass == "ElementBanner" ||
                        subclass == "ElementBasis"
                    ) {
                        element.splice(this.currentIndex + 1, 0, insertObj);
                        this.currentIndex++;
                    }
                    break;

                /* 组件上移 */
                case "move-up":
                    if (this.currentIndex === 0) {
                        return this.$message.error("该组件不能移动！");
                    } else if (this.currentIndex <= 1) {
                        return this.$message.warning("已经移动到最顶部啦！");
                    }
                    this.currentIndex--;
                    let up = element.splice(this.currentIndex, 1);
                    element.splice(this.currentIndex + 1, 0, ...up);
                    break;

                /* 组件下移 */
                case "move-down":
                    if (this.currentIndex === 0) {
                        return this.$message.error("该组件不能移动！");
                    } else if (this.currentIndex >= element.length - 1) {
                        return this.$message.warning("已经移动到最底部啦！");
                    }

                    this.currentIndex++;
                    let down = element.splice(this.currentIndex, 1);
                    element.splice(this.currentIndex - 1, 0, ...down);
                    break;

                /* 复制组件 */
                case "copy":
                    if (this.currentIndex === 0)
                        return this.$message.error("该组件不能复制！");
                    let copy = JSON.parse(
                        JSON.stringify(element[this.currentIndex])
                    );

                    element.splice(
                        this.currentIndex,
                        0,
                        Object.assign(copy, { id: Date.now() })
                    );
                    this.currentIndex++;
                    break;

                /* 删除组件 */
                case "remove":
                    if (this.currentIndex === 0)
                        return this.$message.error("该组件不能删除！");

                    // 删除选中的组件
                    this.$delete(this.elementComponents, this.currentIndex);
                    this.currentIndex--;
                    break;
            }
        },
        /**
         *  重置
         */
        reset() {
            this.$confirm("此操作将永久重置该模板, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    Object.assign(this.$data, this.$options.data());
                    this.$message({
                        type: "success",
                        message: "重置成功!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消重置"
                    });
                });
        },
        /**
         *  TODO: 保存
         */
        save() {
            this.dialogFormVisible = true;
            console.log(this.elementComponents);
        },
        /**
         *  TODO: 发布
         */
        release() {
            this.$confirm("是否立即发布?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    //...

                    this.$message({
                        type: "success",
                        message: "发布成功!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消发布"
                    });
                });
        },
        /**
         *  拖动组件时同时更改索引位置
         */
        end(evt) {
            this.currentIndex = evt.newIndex;
        },
        /**
         *  阻止火狐浏览器拖动弹窗
         */
        _stopWindow() {
            document.body.ondrop = function(event) {
                event.preventDefault();
                event.stopPropagation();
            };
        }
    },
    mounted() {
        this._stopWindow();
    }
};
</script>

<style scoped>
.mini-program {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    height: 100%;
    background-color: #e9f7fd;
    font-family: Avenir, Helvetica, Arial, "sans-serif";
}

.container {
    padding-bottom: 55px;
}

.auto-scroll {
    overflow: auto;
    padding: 0 15px;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.draggable {
    width: 100%;
}

.border-dashed:hover {
    position: relative;
    outline: 1px dashed #25a589;
    z-index: 3;
}

.border-active {
    position: relative;
    outline: 1px dashed #25a589;
    z-index: 3;
}

.right-panel {
    width: 720px;
    background-color: #fff;
    box-sizing: border-box;
    padding: 20px 15px 0;
    border-left: 1px solid #25a589;
}

/* ELEMENT_UI STYLE */
.dialog /deep/ .el-textarea__inner {
    width: 200px;
    display: inline-block;
}
.dialog /deep/ .el-form-item__label {
    display: inline-block;
}

.dialog /deep/ .el-form-item__content {
    margin-left: 0;
}
.mini-program /deep/ .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    height: 120px;
    display: inline-flex;
    align-items: center;
}
.mini-program /deep/ .avatar-uploader .el-upload:hover {
    border-color: #409eff;
}
.mini-program /deep/ .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 120px;
    height: 120px;
    line-height: 120px;
    text-align: center;
}
.mini-program /deep/ .avatar {
    width: 120px;
    display: block;
}
</style>



